.audio-panel-control{width: 35px;height: 35px;border-radius: 50%;background: rgba(0,0,0,.8) url(data:image/png;base64,) no-repeat center;background-size: 25px;margin-left: 10px;margin-bottom: 10px;overflow: hidden}
